import React from "react";

import { Container, Row, Col } from "react-bootstrap";

import {
  timeline,
  timelineItem,
  timelineArrow,
} from "../styles/modules/timeline.module.css";

import ResumeData from "../resumeData";

const { employment } = ResumeData;

function Organizations() {
  const isDarkTheme = JSON.parse(localStorage.getItem("dark"));

  return (
    <Container className="py-3">
      <Row>
        <Col className="mx-auto" lg={10}>
          <ul className={timeline}>
            {employment.map((data, index) => {
              return (
                <li
                  key={`{'company-'}${index}`}
                  className={`${timelineItem} ${
                    isDarkTheme ? "bg-dark" : "bg-white"
                  } rounded mr-2 p-4 shadow`}
                >
                  <div className={timelineArrow}></div>
                  <h2 className="h5 mb-0">{data.companyName}</h2>
                  <span className="text-muted">
                    Role: {data.position} ({data.duration})
                  </span>
                  <br />
                  <span className="text-muted">
                    Company Info: {data.companyInfo}
                  </span>
                  <br />

                  <div className="mt-3">
                    {data.projects.map((project, index) => {
                      return (
                        <React.Fragment key={`{'project-'}${index}`}>
                          <div className="mt-1 font-weight-normal">
                            <span className="font-weight-bolder">Client:</span>{" "}
                            {project.client}
                          </div>
                          <div className="mt-1 font-weight-normal">
                            <span className="font-weight-bolder">
                              Description:
                            </span>{" "}
                            {project.description}
                          </div>
                          <div className="mt-1 font-weight-normal">
                            <span className="font-weight-bolder">
                              Project Title:
                            </span>{" "}
                            {project.name}
                          </div>
                          <div className="mt-1 font-weight-normal">
                            <span className="font-weight-bolder">
                              Technology Used:
                            </span>{" "}
                            {project.technology}
                          </div>
                          <span className="font-weight-bolder">
                            Responsibility:
                          </span>
                          <ul>
                            {project.responsibility.map((detail, i) => {
                              return (
                                <li key={`{'project-responsibility'}${i}`}>
                                  {detail}
                                </li>
                              );
                            })}
                          </ul>
                        </React.Fragment>
                      );
                    })}
                  </div>
                </li>
              );
            })}
          </ul>
        </Col>
      </Row>
    </Container>
  );
}

export default Organizations;
