import React from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLinkedin, faGithub } from '@fortawesome/free-brands-svg-icons';
import { faFileAlt } from '@fortawesome/free-solid-svg-icons';

import { socialLink } from '../styles/modules/socialLinks.module.css';

const socialMediaLinks = [
  {
    'tooltip': 'LinkedIn',
    'icon': faLinkedin,
    'link': 'https://www.linkedin.com/in/sudhirjedhe/'
    
  },
  {
    'tooltip': 'GitHub',
    'icon': faGithub,
    'link': 'https://github.com/sudhir-jedhe'
  },

  {
    'tooltip': 'Resume',
    'icon': faFileAlt,
    'link': 'https://sudhir-jedhe.github.io/sampleResume/'
  }
];

function SocialMediaLinks() {
  return (
    <h2 className="mt-3">
      {
        socialMediaLinks.map(({ link, icon, tooltip }) => {
          return (
            <a
              key={tooltip}
              rel="noopener noreferrer"
              target="_blank" href={link}
              className={`${socialLink} mt-2 mr-3`}
            >
              <FontAwesomeIcon icon={icon} title={tooltip} />
            </a>
          )
        })
      }
    </h2>
  )
}

export default SocialMediaLinks;
