import React from 'react';

import { Container, Row, Col } from 'react-bootstrap';

import { timeline, timelineItem, timelineArrow } from '../styles/modules/timeline.module.css';


import ResumeData from "../resumeData";

const { education } = ResumeData;


function Schools() {
  const isDarkTheme = JSON.parse(localStorage.getItem("dark"));

  return (
    <Container className="py-3">
      <Row>
        <Col className="mx-auto" lg={10}>
          <ul className={timeline}>
            {
              education.map((data, index) => {
                return (
                  <li key={`${data.course}-index`} className={`${timelineItem} ${isDarkTheme ? 'bg-dark' : 'bg-white'} rounded mr-2 p-4 shadow`}>
                    <div className={timelineArrow}></div>
                    <h2 className="h5 mb-0">{data.course}</h2>
                    <span className="small">{data.duration}</span> 
                    <br />
                    <small className="text-muted">{data.university}</small>
                    <br />
                    <small className="text-muted">{data.gardeMarks}</small>
                  </li>
                )
              })
            }
          </ul>
        </Col>
      </Row>
    </Container>
  )
}

export default Schools;
