    return (
        <div id="contact">
            <Container fluid={true}>
                <Row className="pt-5 pb-3">
                    <Col>
                        <h3 className="display-4 font-weight-normal text-center">
                            Get In Touch
                            <hr className="w-75" />
                        </h3>
                    </Col>
                </Row>
                <Card className={`${isDarkTheme ? 'bg-dark' : 'bg-white'} "shadow mx-auto font-weight-normal"`} style={{ width: "90%" }}>
                    <Card.Body>
                        <Row>
                            <Col>
                                <div className="text-center font-weight-normal">
                                    Should you have any question or need further information, please do not hesitate to contact me, my inbox is always open and I'll try my best to get back to you!
                                    <hr className="w-75" />
                                </div>
                            </Col>
                        </Row>
                        <ContactInfo />
                    </Card.Body>
                </Card>
            </Container>
        </div>
    )
