import React from 'react';

import { Container, Row, CardDeck, Card } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFileCode, faServer, faDatabase, faCode } from '@fortawesome/free-solid-svg-icons';

const skillsData = [
  {
    'label': 'Frontend',
    'icon': faFileCode,
    'skills': [
      'HTML5', 'CSS3', 'SASS', 'jQuery', 'JavaScript', 'React JS', 'Vue JS',
    ]
  },
  {
    'label': 'Backend',
    'icon': faServer,
    'skills': [
      'Node JS', 'Python', 'PHP', 'Express', 'GraphQL', 'REST API', 'Firebase'
    ]
  },
  {
    'label': 'Database',
    'icon': faDatabase,
    'skills': [
      'MySQL', 'MongoDB', 'Firestore'
    ]
  },
  {
    'label': 'Others',
    'icon': faCode,
    'skills': [
      'Webpack', 'Gulp', 'SVN', 'Git', 'Heroku', 'Docker', 'Electron JS'
    ]
  }
];

function TechStack() {
  const isDarkTheme = JSON.parse(localStorage.getItem("dark"));

  return (
    <Container>
      <Row>
        <CardDeck>
          {
            skillsData.map(data => {
              return (
                <Card key={data.label} className={`${isDarkTheme ? 'bg-dark' : 'bg-white'} shadow `}>
                  <Card.Body>
                    <Card.Title>
                      <FontAwesomeIcon icon={data.icon} /> {data.label}
                    </Card.Title>
                    <hr />
                    <div>
                      {
                        data.skills.map((skill) => {
                          return (
                            <div key={skill}
                              className={`${isDarkTheme ? 'bg-white text-dark' : 'bg-dark text-white'} d-inline-block p-2 mr-2 mb-2 rounded`}
                            >
                              {skill}
                            </div>
                          )
                        })
                      }
                    </div>
                  </Card.Body>
                </Card>
              )
            })
          }
        </CardDeck>
      </Row>
    </Container>
  )
}

export default TechStack;
