import React from 'react'

import { Container, Row, Col } from 'react-bootstrap';

function Footer() {
    const isDarkTheme = JSON.parse(localStorage.getItem("dark"));

    return (
        <footer className={`${isDarkTheme ? 'bg-dark' : 'bg-white'} mt-5`}>
            <Container fluid={true}>
                <Row className="border-top justify-content-between p-3">
                    <Col className='text-center'>
                        Built with React <span role="img" aria-label="love">💖</span> by Sudhir Jedhe.
                    </Col>
                </Row>
            </Container>
        </footer>
    );
}

export default Footer
