import React from 'react';

import { Container, Row, Col, CardDeck, Card, Button } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub } from '@fortawesome/free-brands-svg-icons';
import { faExternalLinkAlt } from '@fortawesome/free-solid-svg-icons';
import { socialLink } from '../styles/modules/socialLinks.module.css';

const projects = [
    {
        'title': 'React To Do List',
        'description': 'A self learning project to implement React, React Router and PWA.',
        'techUsed': 'React, Bootstrap, React router, API, Styled Components',
        'preview': 'https://sudhir-jedhe.github.io/myresume/',
        'sourceCode': 'https://sudhir-jedhe.github.io/myresume/'
    },
    {
        'title': 'RESTful Shopping API',
        'description': 'Restful API architecture with authenticated CRUD operations on mongoDB.',
        'techUsed': 'Node, Express, REST, API, JWT, MongoDB',
        'preview': 'https://sudhir-jedhe.github.io/myresume/',
        'sourceCode': '',
    },
];

function ProjectCards() {
    const isDarkTheme = JSON.parse(localStorage.getItem("dark"));

    return (
        <Container>
            <Row>
                <CardDeck>
                    {
                        projects.map(data => {
                            return (
                                <Card key={data.title} className={`${isDarkTheme ? 'bg-dark' : 'bg-white'} shadow`}>
                                    <Card.Body>
                                        <Card.Title>
                                            {data.title}
                                        </Card.Title>
                                        <hr />
                                        <small className="text-muted">
                                            {data.techUsed}
                                        </small>
                                        <br />
                                        <a rel="noopener noreferrer"
                                            target="_blank"
                                            href={data.sourceCode}
                                            className={`${socialLink}`}
                                        >
                                            <FontAwesomeIcon icon={faGithub} className="mr-2" />
                                        </a>

                                        {
                                            (data.preview) ? (
                                                <a rel="noopener noreferrer"
                                                    target="_blank"
                                                    href={data.preview}
                                                    className={`${socialLink}`}
                                                >
                                                    <FontAwesomeIcon icon={faExternalLinkAlt} />
                                                </a>) : (null)
                                        }
                                        <Card.Text className="mt-2">
                                            {data.description}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>
                            )
                        })
                    }
                </CardDeck>
            </Row>
            <Row>
                <Col className="mt-4 text-center">
                    <Button
                        variant={`${isDarkTheme ? 'outline-light' : 'outline-dark'}`}
                        rel="noopener noreferrer"
                        target="_blank"
                        href="https://github.com/sudhir-jedhe?tab=repositories"
                    >
                        <FontAwesomeIcon icon={faGithub} className="mr-2" />More on Github
                    </Button>
                </Col>
            </Row>
        </Container>
    )
}

export default ProjectCards;
