import React, { useState } from 'react';

import { Navbar, Nav } from 'react-bootstrap';

import { smoothScroll } from '../utils/smoothScroll';

function Header() {
    const isDarkTheme = JSON.parse(localStorage.getItem("dark"));
    const [expanded, setExpanded] = useState(false);

    // To handle Toggling of the navbar expansion and smooth scrolling
    const scrollAndToggle = (elemId) => {
        if (!expanded) {
            smoothScroll(elemId);
            return;
        }

        setExpanded(false);
        // Tried using useEffect but didn't work
        // So using setTimeout
        setTimeout(() => {
            smoothScroll(elemId)
        }, 500);
    }

    return (
        <Navbar expanded={expanded} sticky="top" variant={`${isDarkTheme ? "dark" : "light"}`} bg={`${isDarkTheme ? "dark" : "light"}`} expand="lg">
            <Navbar.Brand onClick={() => scrollAndToggle('home')} className="ml-2"> Sudhir Jedhe</Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" onClick={() => setExpanded(expanded ? false : "expanded")} />
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="ml-auto smooth-scroll">
                    <Nav.Link onClick={() => scrollAndToggle('home')}>Home</Nav.Link>
                    <Nav.Link onClick={() => scrollAndToggle('experience')}>Experience</Nav.Link>
                    <Nav.Link onClick={() => scrollAndToggle('education')}>Education</Nav.Link>
                    <Nav.Link onClick={() => scrollAndToggle('projects')}>Projects</Nav.Link>
                    <Nav.Link onClick={() => scrollAndToggle('skills')}>Skills</Nav.Link>
                    <Nav.Link onClick={() => scrollAndToggle('about')}>About</Nav.Link>
                    <Nav.Link onClick={() => scrollAndToggle('contact')}>Contact</Nav.Link>
                </Nav>
            </Navbar.Collapse>
        </Navbar>
    )
}

export default Header;
